
import { Component, Vue, Watch } from "vue-property-decorator";
import Tabs from "@/components/Tabs.vue";
import typeList from "@/constants/typeList";
import * as echarts from "echarts";
import dayjs from "dayjs";
import Decimal from "decimal.js";
import { EChartsType } from "echarts";

@Component({
  components: {
    Tabs,
  },
})
export default class ChartsView extends Vue {
  myChart?: EChartsType;
  type = "-";
  typeList = typeList;
  month = dayjs(new Date()).format("YYYY-MM");
  get recordList() {
    return this.$store.state.recordList as RecordItem[];
  }
  get monthRecord() {
    return this.recordList.filter(
      (r) => r.createdAt!.substring(0, 7) === this.month
    );
  }
  get subRecord() {
    return this.monthRecord.filter((r) => r.value === "-");
  }
  get addRecord() {
    return this.monthRecord.filter((r) => r.value === "+");
  }
  get subMoney() {
    let sum = new Decimal(0);
    for (let r of this.subRecord) {
      sum = sum.add(new Decimal(r.amount));
    }
    return sum.toNumber();
  }
  get addMoney() {
    let sum = new Decimal(0);
    for (let r of this.addRecord) {
      sum = sum.add(new Decimal(r.amount));
    }
    return sum.toNumber();
  }
  get sumMoney() {
    return Decimal.sub(this.addMoney, this.subMoney).toNumber();
  }
  get dateList() {
    let dateSubList = this.mergedDate(this.subRecord);
    let dateAddList = this.mergedDate(this.addRecord);
    if (this.type === "-") {
      return dateSubList;
    } else if (this.type === "+") {
      return dateAddList;
    } else {
      return [];
    }
  }
  mergedDate(dateList: RecordItem[]) {
    const result = dateList.reduce((result: { [key: string]: number }, r) => {
      if (result[r.selectedtag]) {
        let sum = new Decimal(result[r.selectedtag]);
        sum = sum.add(new Decimal(r.amount));
        result[r.selectedtag] = sum.toNumber();
      } else {
        result[r.selectedtag] = r.amount;
      }
      return result;
    }, {});
    const newArr = [];
    for (let selectedtag in result) {
      newArr.push({
        value: result[selectedtag],
        name: selectedtag,
      });
    }
    return newArr;
  }

  created() {
    this.$store.commit("fetchRecords");
  }
  mounted() {
    // 初始化图表
    this.myChart = echarts.init(this.$refs.chart as HTMLDivElement);
    this.myChart.setOption(this.option);
  }
  @Watch("dateList")
  onValueChanged() {
    this.myChart?.setOption({
      series: [{ data: this.dateList }],
    });
  }
  option = {
    tooltip: {
      trigger: "item",
    },
    legend: {
      top: "5%",
      left: "center",
    },
    series: [
      {
        name: "Access From",
        type: "pie",
        radius: ["40%", "70%"],
        avoidLabelOverlap: false,
        itemStyle: {
          borderRadius: 10,
          borderColor: "#fff",
          borderWidth: 2,
        },
        label: {
          show: false,
          position: "center",
        },
        emphasis: {
          label: {
            show: true,
            fontSize: 30,
            fontWeight: "bold",
          },
        },
        labelLine: {
          show: false,
        },
        data: this.dateList,
      },
    ],
  };
}
