
import Vue from "vue";
import { Component } from "vue-property-decorator";

@Component
export default class NumberPad extends Vue {
  amount = "";
  inputContent(event: Event) {
    //记账输入方法
    const button = event.target as HTMLButtonElement;
    const input = button.textContent!; //使用'!'表示确保变量的值不为null或undefined
    const length = this.amount.length;
    if (button.className === "delete" || button.nodeName === "svg") {
      //删除按钮
      this.amount = this.amount.slice(0, -1);
    } else if (button.className === "ok") {
      //ok提交按钮
      if (this.amount && this.amount !== "0" && this.amount !== "0.") {
        this.$emit("update:amount", this.amount);
        this.amount = "";
      }
    } else {
      if (length >= 16) {
        //最长16位数字
        return;
      }
      if (this.amount === "" && input === ".") {
        //不能直接输入小数点
        return;
      }
      if (this.amount.indexOf(".") >= 0 && input === ".") {
        //不能重复输入小数点
        return;
      }
      if (this.amount === "0") {
        if (input === ".") {
          this.amount += input;
        } else {
          this.amount = input;
        }
      } else {
        this.amount += input;
      }
    }
  }
  mounted() {
    const numPad = document.querySelectorAll(".buttons>button");
    numPad.forEach((button) => {
      button.addEventListener("click", (e) => {
        this.inputContent(e);
      });
    });
  }
}
